export default {
    data() {
        return {
            blogs: []
        };
    },
    methods: {
        async searchBlog(keyword) {
            let blogs = await this.$http(
                "/blog-service/search?keyword=" + encodeURIComponent(keyword),
                "GET"
            );
            this.blogs = blogs;

            this.$showToast("已载入");
        },
        handleLeft() {
            uni.switchTab({
                url: "../index/index"
            });
        },
        handleRight(value) {
            this.searchBlog(value);
        }
    }
};
